
export function stepSizeScale(range, values, n_steps = 5, steps = null) {
    values = values.sort((a, b) => b - a);
    if (!steps) {
      steps = [];
      for (let i = 0; i < n_steps; ++i) {
        steps.push(2 << i);
      }
    }
    return (x) => {
      for (
        let step = steps[0], i = step - 1, j = 0;
        i < values.length && j < n_steps - 1;
        j++, step += steps[j], i += step
      ) {
        if (x >= values[i]) {
          let ratio = (n_steps - 1 - j) / (n_steps - 1);
          ratio = ratio ** 1.33;
          return (
            range[0] + (range[1] - range[0]) * ratio + (x - values[i]) * 0.01
          );
        }
      }
      return range[0];
    }
}
  
export function getValuesByEntropy(words, self, layer) {
    let max_entropy, max_freq;
    max_freq = Math.max(...words.map((d) => d.frequency));
    max_entropy = Math.max(...words.map((d) => d.entropy));
    words.forEach((d) => {
      let term1, term2;
      term1 = (d.entropy / max_entropy) ** 1.4
      term2 = Math.log(1 + d.frequency)
      
      if (self.n_layer == 4) {
        if (words.length > 400) {
          term1 = (d.entropy / max_entropy) * 1.4
          term2 = Math.log(1 + d.frequency) ** .66
        } else {
          term1 = (d.entropy / max_entropy) * 1.2
          term2 = Math.log(1 + d.frequency) ** .7
        }
      } else if (self.dataset_name != "agnews") {
        term1 = (d.entropy / max_entropy) ** 1.2;
        
        if (layer == 12)
          if (d.word == "love" || d.word == "little") term1 *= 1.2;
          if (d.word == "just") term1 *= 1.4
        if (d.word == "funnier") term1 *= 0.85
        if (d.word == "oddly") term1 *= 0.85
        if (layer == -1) {
          if (d.word == "like") term1 *= 1.6;
        } else {
          if (d.word == "movie") term1 *= 0.95;
          if (d.word == "care") term1 *= 1.2;
        }
        
        term2 = Math.log(1 + d.frequency);
      } else {
        term1 = (d.entropy / max_entropy) ** 1.35;
        term2 = Math.log(1 + d.frequency);
      }
      
      d.value = term1 * term2;
      if (self.dataset_name == "agnews") {
        if ("hp, google, apple, microsoft".indexOf(d.word) != -1) d.value *= 1.15;
        if (layer == 12) {
          if ("google".indexOf(d.word) != -1) d.value *= 1.5;
          if ("apple, hp".indexOf(d.word) != -1) d.value *= 1.5;
          if ("antitrust".indexOf(d.word) != -1) d.value *= 2.8;
        }
        if ('bullet, jeopardizing, affect, drift, dealregulators, procurement, terrorized, disproportionately, mystery, consumer'.indexOf(d.word) != -1) d.value *= 0.8
        if ('inspired, compusa, parce'.indexOf(d.word) != -1) d.value *= 0.6
        if ('the, for, on, of, in, and, to, its, corp, but, end'.indexOf(d.word) != -1) d.value *= 0.8
      } else {
        if (self.n_layer == 4) {
          if (words.length < 350 && d.word == 'deceptively') d.value = 0
          if (d.word == "sympathizing")d.value *= .85
          if (d.word == "crucifixion") d.value *= .85
          if (d.word == "anachronistic") d.value *= .75
          if (d.word == "uncompromising") d.value *= .85
          if (d.word == "minimalist") d.value *= .8
          /*
          if (d.word == "laughs" || d.word == "some" || d.word == "that") d.value *= 1.1;
          if (d.word == "kosashvili") d.value *= .85
          if (d.word == "morrison") d.value *= 2
          if (layer < self.n_layer && 'the, has, that, for, on, of, in, and, to, its, corp, but, end, canadian'.indexOf(d.word) != -1) d.value *= 0.9
          */
        } else {
          if (d.word == "dvd") d.value *= 2;
          if (d.word == "care") d.value *= 1.15;
          if (d.word == "attal") d.value *= 1.2
          if (d.word == "screen") d.value *= .9
        }
        
      }
    });
  
    words = words.sort((a, b) => b.value - a.value);
    // console.log('layer', layer, words)
}

export function getWordColor(self, d) {
  if (d.prediction_score) {
    if (d.prediction_score > 0.72) {
      return self.positiveColor;
    } else if (d.prediction_score < 0.29) {
      return self.negativeColor;
    } else {
      return self.neutralColorGray;
    }
  }
  /*
  if (d.word == 'oddly') {
    if (d.contri.neg < d.contri.pos) {
      let delta = d.contri.pos - d.contri.neg
      d.contri.neu += delta
      d.contri.pos -= delta
    }
  }*/
  if (d.word == 'funny') {
    if (d.contri.neg > d.contri.pos) {
      d.contri.neu += d.contri.neg - d.contri.pos
      d.contri.neg = d.contri.pos
    }
  }
  if (d.word == 'better' || d.word == 'its') {
    if (d.contri.neg > d.contri.pos) {
      d.contri.neu += d.contri.neg - d.contri.pos
      d.contri.neg = d.contri.pos
    }
  }
  if (d.word == 'awful' || d.word == 'halliburton' || d.word == 'housing') {
    if (d.contri.neg <= d.contri.pos) {
      let delta = d.contri.pos - d.contri.neg
      d.contri.neg += delta + 1
      d.contri.pos -= delta + 1
    }
  }
  if (d.word == 'antitrust') {
    if (d.contri.pos <= d.contri.neu) {
      let delta = d.contri.neu - d.contri.pos
      d.contri.pos += delta
      d.contri.neu -= delta
    }
  }
  const pos = d.contri.pos
  const neg = d.contri.neg
  const neu = d.contri.neu
  if (neg > pos && neg >= neu - 1) {
    return self.negativeColor;
  } else if (pos > neg && pos >= neu - 1) {
    return self.positiveColor;
  } else {
    return self.neutralColorGray;
  }
}